export const GEOSERVER_LAYER_EXAMPLE_CODE = 
`import React, { Component } from 'react';
import GeoserverLayer from 'easyolmaps/layer/GeoserverLayer';
import MapContainer from '../components/MapContainer';
import { Map, View } from 'ol';
import TileLayer from 'ol/layer/Tile';
import OSM from 'ol/source/OSM';
import { fromLonLat } from 'ol/proj';

class GeoserverLayerExample extends Component {
    constructor(props) {
        super(props);
        this.map = null;
        this.mapElement = null;
    }
    componentDidMount() {
        this.map = new Map({
            target: this.mapElement,
            layers: [
                new TileLayer({ source: new OSM() }),
                /* See how naturally integrates with Openlayers. Nice, isn't it? */
                new GeoserverLayer('tiger:poly_landmarks', {
                    url: 'http://gisteco.epsgrau.pe:8080/geoserver/wms'
                })
            ],
            view: new View({
                center: fromLonLat([-73.96168, 40.78606]),
                zoom: 11
            })
        })
    }
    render() {
        return <MapContainer ref={mapElement => this.mapElement = mapElement} />
    }
}`;

export const GEOSERVER_LAYER_FILTERING_EXAMPLE =
`import React, { Component } from 'react';
import GeoserverLayer from 'easyolmaps/layer/GeoserverLayer';
import { Map, View } from 'ol';
import TileLayer from 'ol/layer/Tile';
import OSM from 'ol/source/OSM';
import { fromLonLat } from 'ol/proj';
import MapContainer from '../components/MapContainer';

class GeoserverLayerFilteringExample extends Component {
    constructor(props) {
        super(props);
        this.state = { filter: 'P_FEMALE > 0.51' }
        this.map = null;
        this.mapElement = null;
        this.toppStates = null;
    }
    handleInputChange = (e) => {
        this.setState({ filter: e.target.value });
    }
    componentDidMount() {
        this.toppStates = new GeoserverLayer('topp:states', {
            url: 'http://gisteco.epsgrau.pe:8080/geoserver/wms'
        });
        this.map = new Map({
            target: this.mapElement,
            layers: [
                new TileLayer({ source: new OSM() }),
                this.toppStates
            ],
            view: new View({
                center: fromLonLat([-102.04102, 39.90234]),
                zoom: 4
            })
        })
    }
    render() {
        const { filter } = this.state;
        return <div>
            <MapContainer ref={mapElement => this.mapElement = mapElement} />
            <div className='py-2'>
            <input
                className='mr-2'
                style={{ width: '500px' }}
                type='text'
                placeholder='Enter a valid ECQL expression here'
                value={filter}
                onChange={this.handleInputChange}
            />
                <button type='button' onClick={() => {
                    /* No need of more than one line to apply a filter. Isn't that cool!? */
                    this.toppStates.filter(filter);
                }}>Apply</button>
            </div>
        </div>
    }
}`;